
.Soundplayer{
  position: fixed;
  height: 45px;
  background: #f7fafc;
  /*background: #e6eaec;*/
  bottom: 0px;
  z-index: 3;
  border-top: 1px solid #e8e8e8;
  display: grid;
  width: 100%;
  left: 0;
  -webkit-box-shadow: 3px -7px 21px -14px rgba(0,0,0,0.15);
  -moz-box-shadow: 3px -7px 21px -14px rgba(0,0,0,0.15);
  box-shadow: 3px -7px 21px -14px rgba(0,0,0,0.15);
}


.Soundplayer.help_section{
  width: calc(100% - 19vw);
  left: 19vw;
}

.SoundplayerContent{
  width: calc(100% - 50px);
  padding: 0 25px;
  display: grid;
  grid-template-columns: 80px 1fr minmax(50px, 200px);
}

.SoundplayerContent div{
  /*border: 1px solid red;*/
  height: 100%;
  width: 100%;
  display: grid;
  align-items: center;
  justify-items:center;
}

.SoundPlayerRola{
  grid-template-rows: 1fr 6px;
}

.SoundPlayerRola p{
  margin: 0;
  width: 100%;
  text-align: left;
  font-size: 14px;
  color: gray;
}

.SoundPlayerRola span{
  background: #c3c3c3;
  width: 100%;
  height: 100%;
  border-radius: 4px;
}


.toggleSoundPButtom{
  cursor: pointer;
}

.toggleSoundPButtom i{
  color: gray;
}

















@media only screen and (max-width: 768px) {


  .Soundplayer.help_section{
    left: 0 !important;
    width: 100% !important;
  }

  .SoundplayerContent{
    grid-template-columns: 60px 1fr !important;
    width: calc(100% - 30px);
    padding: 0 15px;
  }

  .SoundPlayerRola p{
    display: block;
    margin-left: 2px;
    max-width: 200px;
    overflow: hidden;
    padding-left: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    justify-self: baseline;
  }

  .siriContainer{
    display: none !important;
  }



}































